body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.torch {
    position: absolute;
    bottom: 450px;
    left: 400px;
    -webkit-transform-origin: 50px 0px;
            transform-origin: 50px 0px;
  }
  
  .torch .arm {
    position: absolute;
    bottom: 0px;
    left: 45px;
    margin: 0;
    padding: 0;
    height: 380px;
    width: 10px;
    background-color: black;
    z-index: 100;
  }
  
  .torch img {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100px;
    z-index: 200;
  }
  
.element {
    position: absolute;
    bottom: 450px;
    left: 375px;
    height: 420px;
    -webkit-transform-origin: 75px 420px;
            transform-origin: 75px 420px;
  }
  
.app {
  position: relative;
  display: -webkit-flex;
  display: flex;
  z-index: 0;
}

.wheel {
  min-height: 900px;
  min-width: 900px;
  height: 900px;
  width: 900px;
  max-height: 900px;
  max-width: 900px;
}

.wheel-control {
  padding-top: 100px;
}

.wheel-control h3, .wheel-control button {
    width: 250px;
    height: 40px;
    text-align: center;
}
